import {
  GENDER,
  TEAM,
  SUBMIT_PROFILE_STATUS
} from 'app/constants/constants';
import moment from 'moment';
import React from 'react';
import { shorten } from 'app/utilities/shorten';

export const CustomColumnsEmployee = ({ Action, page, pageSize }) => {
  const columns = [
    {
      title: "STT",
      align: "center",
      maxWidth: "55px",
      minWidth: "55px",
      render: (rowData) => rowData.tableData?.id + 1 + page * pageSize,
    },
    {
      title: "Thao tc",
      field: "custom",
      align: "center",
      maxWidth: "110px",
      minWidth: "110px",
      render: (rowData) => <Action rowData={rowData} />
    },
    {
      title: "M nhn vin",
      field: "code",
      align: "center",
      maxWidth: "100px",
      minWidth: "100px",
    },
    {
      title: "Tn nhn vin",
      field: "name",
      align: "left",
      maxWidth: "150px",
      minWidth: "150px",
      render: (rowData) => shorten(rowData.name, 22),
    },
    {
      title: "Ngy sinh",
      field: "dateOfBirth",
      align: "center",
      maxWidth: "100px",
      minWidth: "100px",
      render: (rowData) => moment(rowData.dateOfBirth).format("DD/MM/YYYY"),
    },
    {
      title: "Gii tnh",
      field: "gender",
      align: "center",
      maxWidth: "80px",
      minWidth: "80px",
      render: (rowData) => {
        const nameGender = GENDER.find((item) => item.id === rowData.gender);
        return nameGender ? nameGender.name : "";
      },
    },
    {
      title: "Nhm",
      field: "team",
      align: "center",
      maxWidth: "150px",
      minWidth: "150px",
      render: (rowData) => {
        const nameTeam = TEAM.find((item) => item.id === rowData.team);
        return nameTeam ? nameTeam.name : "";
      },
    },
    {
      title: "in thoi",
      field: "phone",
      align: "center",
      maxWidth: "100px",
      minWidth: "100px",
    },
    {
      title: "Email",
      field: "email",
      align: "center",
      maxWidth: "180px",
      minWidth: "180px",
      render: (rowData) => shorten(rowData.email, 26),
    },
    {
      title: "a ch",
      field: "address",
      align: "center",
      maxWidth: "150px",
      minWidth: "150px",
      render: (rowData) => shorten(rowData.address, 22),
    },
    {
      title: "Trng thi",
      field: "submitProfileStatus",
      align: "center",
      maxWidth: "150px",
      minWidth: "150px",
      render: (rowData) => {
        const nameStatus = SUBMIT_PROFILE_STATUS.find(
          (item) => item.id === rowData.submitProfileStatus
        );
        return nameStatus ? nameStatus.name : "Li";
      },
    },
  ];

  return columns;
};

